@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;800&display=swap');

body{
	margin: 0;
	font-family: 'Inter';
	font-weight: 400;
	background: #eef;
	min-height: 100vh;
}

body.modal-open{
	overflow: hidden;
}

.title{
	font-family: 'Inter', sans-serif;
	font-weight: 800;
}

#signed-out-flow{	
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin: 5vh 18vh;
}

p{
	font-size: 21px;
}

h3{
	margin: 2vh;
}
h5{
	margin: 0;
}
#token-data{
	font-size: 10px;
}

.info_in_main{
	font-size: 16px;
}
button{
	background: white;
	font-family: 'Inter';
	font-weight: 400;
	font-size: 17px;
	border: 2px solid black;
	height: 5vh;
}

button:hover{
	background: #ddd;
	/*box-shadow:  2px 4px #000*/;
}

#signed-in-flow{
	display: flex;
	flex-direction: column;
	gap: 30px;
	padding: 5vh 10vh;
}

label {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  text-align: right;
  width: 400px;
  line-height: 26px;
  margin-bottom: 10px;
}

input {
  height: 20px;
  flex: 0 0 200px;
  margin-left: 10px;
}

#exit{
	display: flex;
	justify-content: center;
}

#sign-out-button:hover{
	background: #B33030;
	color: white;
}

#imgs{
	display: flex;
	flex-flow: wrap ;
	gap: 20px;
}

.modal_bg{
	width:100%;
	height:100vh;
	background-color :rgba(0,0,0,0.6);
	position:absolute;
	top: 0;
	display:flex;
	justify-content:center;
	align-items:center; 
	overflow: auto;
}

.modal{
	font-family: 'Inter';
	font-size: 3.5vh;
	background-color:white;
	border-radius: 1vh;
	display: flex;
	flex-direction: column;
	padding: 3vh 2vw;
	gap: 2vh;
	overflow-y: auto;
}

.modal_inputs{
	height: auto;
	width: 50%;
}

#close_modal{
	width: 10%;
}

#close_modal:hover{
	background-color: #FC4F4F;
	color: white;
}